'use strict';

let util = require('../core/util');

/**
 * @class AccountService
 */
module.exports = class AccountService {

    constructor(param) {
        const init = util.buildInitParam(param);

        this.EVENT_ACCOUNT_FETCHED = 'account:fetched';
        this.EVENT_ACCOUNT_UPDATED = 'account:updated';

        this._EventBus = init._EventBus;

        this._customerUrl = function(customerId){
            return 'customers/' + customerId;
        }
    }

    /**
     * get customer
     * @param {String} customerId 
     */
    getCustomerById(customerId){
        var _this = this;
        return util.makeCTGetRequest(this._customerUrl(customerId)).then(function(res){
            if(res.id){
                _this._EventBus.dispatch(_this.EVENT_ACCOUNT_FETCHED, res);
            }
            return res;
        });
    }

    /**
     * Update Customer Info
     * @param {Object} customerMeta 
     * @param {Array} actions 
     */
    updateCustomer(customerMeta, actions){
        var request = {
            "version":customerMeta.version, 
            "actions":actions
        };
        return util.makeCTPostRequest(this._customerUrl(customerMeta.id), request).then(this._dispatchIsUpdated.bind(this));
    }

    /**
     * Dispatch Customer Updated Event
     * @param {Object} res 
     */
    _dispatchIsUpdated(res){
        if(res.id){
            this._EventBus.dispatch(this.EVENT_ACCOUNT_UPDATED, res);
        }
        return res;
    }

    /**
     * Add Address
     * 
     * @param {Object} customerMeta 
     * @param {Object} address 
     */
    addAddress(customerMeta, address){
        if(!util.typeOf(address, 'object') || !address.country){
            return util.sendValidationError(new Error('Invalid address parameter'));
        }
        return this.updateCustomer(customerMeta, [{
            'action':'addAddress',
            'address':address
        }]);
    }

    /**
     * Edit Existing Address
     * @param {Object} customerMeta 
     * @param {String} addressId 
     * @param {Object} address 
     */
    editAddress(customerMeta, addressId, address){
        return this.updateCustomer(customerMeta, [{
            "action": "changeAddress",
            "addressId": addressId,
            "address": address
        }]);
    }

    /**
     * Add Shipping Address
     * @param {Object} customerMeta 
     * @param {Object} address 
     * @param {Boolean} setDefault 
     */
    addShippingAddress(customerMeta, address, setDefault){
        var _this = this;
        if(!address.externalId){
            address.externalId = ((new Date()).getTime().toString());
        }
        return _this.addAddress(customerMeta, address).then(function(_customer){
            if(_customer.id){
                // Address Added, Now Add this address to Shipping Address Ids
                var _address = util.searchFirstByKeyValue(_customer.addresses, 'externalId', address.externalId);
                if(_address.id){
                    // found Id of newly added Adddress
                    var actions = [{
                        "action": "addShippingAddressId",
                        "addressId": _address.id
                    }];
                    if(setDefault){
                        actions.push({
                            "action": "setDefaultShippingAddress",
                            "addressId": _address.id
                        });
                    }
                    return _this.updateCustomer(_customer, actions);
                }
            }
            return _customer;
        });
    }

    /**
     * Add Billing Address
     * @param {Object} customerMeta 
     * @param {Object} address 
     * @param {Boolean} setDefault 
     */
    addBillingAddress(customerMeta, address, setDefault){
        var _this = this;
        if(!address.externalId){
            address.externalId = ((new Date()).getTime().toString());
        }
        return _this.addAddress(customerMeta, address).then(function(_customer){
            if(_customer.id){
                // Address Added, Now Add this address to Billing Address Ids
                var _address = util.searchFirstByKeyValue(_customer.addresses, 'externalId', address.externalId);
                if(_address.id){
                    // found Id of newly added Adddress
                    var actions = [{
                        "action": "addBillingAddressId",
                        "addressId": _address.id
                    }];
                    if(setDefault || util.typeOf(setDefault, 'undefined')){
                        actions.push({
                            "action": "setDefaultBillingAddress",
                            "addressId": _address.id
                        });
                    }
                    return _this.updateCustomer(_customer, actions);
                }
            }
            return _customer;
        });
    }

    /**
     * Set a Address as Default Shipping Address
     * @param {Object} customerMeta 
     * @param {String} addressId 
     */
    setDefaultShippingAddress(customerMeta, addressId){
        return this.updateCustomer(customerMeta, [{
            'action':'setDefaultShippingAddress',
            'addressId':addressId
        }]);
    }
    
    /**
     * Set a Address as Default Billing Address
     * @param {Object} customerMeta 
     * @param {String} addressId 
     */
    setDefaultBillingAddress(customerMeta, addressId){
        return this.updateCustomer(customerMeta, [{
            'action':'setDefaultBillingAddress',
            'addressId':addressId
        }]);
    }

    /**
     * Add Customer's Company Address
     * 
     * @param {Object} customerMeta 
     * @param {Object} address 
     */
    addCompanyAddress(customerMeta, address){
        var _this = this;
        if(!address.externalId){
            address.externalId = ((new Date()).getTime().toString());
        }
        return _this.addAddress(customerMeta, address).then(function(_customer){
            if(_customer.id){
                // Address Added, Now Add this address to Shipping Address Ids
                var _address = util.searchFirstByKeyValue(_customer.addresses, 'externalId', address.externalId);
                if(_address.id){
                    // found Id of newly added Adddress
                    var actions = [{
                        "action": "setCustomField",
                        "name": "companyAddressId",
                        "value": _address.id
                    }];
                    return _this.updateCustomer(_customer, actions);
                }
            }
            return _customer;
        });
    }

    /**
     * get Customer's Company Address Object
     * @param {Object} customer 
     */
    getCompanyAddress(customer){
        if(!util.typeOf(customer, 'object') || !customer.id){
            return util.sendValidationError(new Error('Invalid customer parameter'));
        }
        let _address = {};
        if(customer.custom && customer.custom.fields && customer.custom.fields.companyAddressId && customer.addresses){
            _address = util.searchFirstByKeyValue(customer.addresses, 'id', customer.custom.fields.companyAddressId);
        }
        return Promise.resolve(_address);
    }

    /**
     * Update Customer Address By Id
     * @param {Object} customerMeta 
     * @param {String} addressId 
     * @param {Object} address 
     */
    updateAddress(customerMeta, addressId, address){
        if(!util.typeOf(address, 'object') || !address.country){
            return util.sendValidationError(new Error('Invalid address parameter'));
        }
        return this.updateCustomer(customerMeta, [{
            'action':'changeAddress',
            'addressId': addressId,
            'address':address
        }]);
    }
}


